.Requests{
    width: 600px;
    background: #08c;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 15px;
    &Product{
        background-color: #ccc;
        border-right: 1px solid #333;
        padding: 10px;
        flex: 0 0 50%;
    }
    &Customer{
        background-color: #ccc;
        padding: 10px;
        flex: 0 0 50%;
    }
    &__actions{
        flex: 0 0 100%;
        display: flex;
        justify-content: flex-end;
        padding: 10px 0 0;
        button {
            background-color: darkgoldenrod;
            padding: 5px 10px;
            color: #fff;
            border: 0;
            outline: none;
            border-radius: 3px;
            margin-left: 10px;
        }
    }
    &__status{
        flex: 0 0 100%;
        text-align: right;
        margin-bottom: 10px;
        color: greenyellow;
    }
}