.AsideMenu{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 250px;
    background-color: #333;

    &__Item{
        border-bottom: 1px solid #fff;
        & a{
            text-decoration: none;
            color: #ccc;
            padding: 10px 15px;
            display: block;
            transition: all 0.3s;
            & .active, :hover{
                background-color: #555;
            }
        }
    }
}